import * as React from "react";
import { BootstrapTable, TableHeaderColumn } from "react-bootstrap-table";
import { pure } from "recompose";

const CustomTable: React.SFC<any> = props => (
    <div>
        <BootstrapTable data={props.searchData}>
            <TableHeaderColumn isKey dataField="id" className="Id">
                Id
            </TableHeaderColumn>
            <TableHeaderColumn dataField="name" className="Id">
                Name
            </TableHeaderColumn>
            <TableHeaderColumn dataField="domain" className="Id">
                Domain
            </TableHeaderColumn>
        </BootstrapTable>
    </div>
);

export default pure(CustomTable);
